import { useHistory } from 'react-router-dom';
import d from '../logo_transparent.png';
const Header = () => {
  const history = useHistory();
  return (
    <div className="con-title">
      <img
        src={d}
        alt=""
        onClick={() => {
          history.push('/');
        }}
        width="100%"
        className="title"
      />
    </div>
  );
};

export default Header;
